export function getOppositeLanguageUrl(path, currentLanguage) {
  const newLanguage = currentLanguage === 'en' ? 'es' : 'en';
  return path.replace(`/${currentLanguage}/`, `/${newLanguage}/`);
}

export function getLanguageFromPath(path) {
  return path ? path.split('/').filter(Boolean)[0] ?? 'en' : 'en';
}